// 燈箱（.lightbox）與 彈出訊息（.popMessage）模組
/*燈箱模組：需要燈箱效果的區塊可在原 class name 之後加 lightbox即可有燈箱效果，原來的class name單純用來控制樣式*/
.lightbox{
  display:none;
  position:fixed;top:50px;left:2%;width:96%;
  height: calc(100vh - 60px);
  padding: 0;margin:0 auto; overflow: auto;overflow-y: auto;
  background:#fff;box-shadow:rgba(0,0,0,.6) 0 0 28px;z-index:8900;box-sizing: content-box; border-radius: 6px;

  .closeThis{
    position: fixed; right:2%; /* top: 54px; */ top: 8px;z-index:99;
    margin: 0 0 4px;padding: 5px; border-radius: 50%;
  }

  &>*{margin-left:3%; margin-right: 3%;}

  &>iframe{display: block;width: 100%;min-height: 100%;overflow: auto;overflow-y: auto;overflow-x: auto;margin: 0 auto;padding:0;border: none;
  }
  &>.head + iframe{ min-height: calc(100% - 90px);}

  .search:last-child, .formX:last-child{box-shadow: none; margin-left: 0;margin-right: 0; padding: 0;
    .formLayout{ margin-left: 30px; margin-right: 30px; width:calc(100% - 60px);}
  }



  .qtTop{position: absolute;top: 0;left: 0;right: 0;margin:0;padding:10px;}
  .qtTop ~ iframe{margin-top: 2em;}


  .head{
    border-bottom: 3px solid $border-color-pale;padding:30px 0 .2em; margin-bottom:15px; overflow: hidden;
    h3{
      margin: 0;padding: 0; color: #000;
      .subhead{
        display: inline-block;font-size: .8em; margin-left: 1em; 
        .pre{color: $font-color; font-size: .8em;}
      }
    }
     &+.openNewWin{float: right; margin-right: 3%; margin-top: -3.9em;}
     & + button{float: right; margin-right: 3%; margin-top: -10px;}

     .labelAndData{ float: right; margin-top: -2.4em;}
  }
  .listTable , .listTbNew{padding: 0;}
  .page + .listTable,
  .page + .listTbNew{margin-top: 8px;}

  .listTable + .page,
  .listTbNew + .page{margin-top: 8px;}

  &>.btnDv{padding: 20px 0 30px; margin-top: 20px;}
  .btnDv.sticky{ margin:0 6px;padding:15px 0;bottom: 6px; border-radius: 4px; box-shadow: rgba(0,0,0,.3) 0 0 6px;}
  
  .dataTable{padding: 0;
    h3{margin: 0 0 5px;}
  }

  .viewImage{ margin: 0 auto;width: 360px;height: 360px;
    img{display: block; margin: 0; width: 100%; height: 100%;object-fit: contain;}
  }

  .coverLightbox{z-index: 8910;}

  .backToNav{
    // display: none;
    margin: 0;
    position: fixed;bottom:30px;
    // right: calc(3vw + 5px);
    right: calc(2vw + 20px);
  }

  &>.openNewWin{ position: absolute; top: 10px; right: 0;}

  @media screen and (min-width: $wwWide){
    // .backToNav{right: auto; left: 50%;margin-left: 470px; }
  }
  
  @media screen and (min-width:1250px){

    width:1200px; left: calc(50vw - 600px);
    .closeThis{ right: calc( 50vw - 600px); } // 20230802
    .backToNav{ right: calc( 50vw - 600px + 20px);} // 20230817
    // .backToNav{right: auto; left: 50%;margin-left: 470px; } 20230817

    &>*{ margin-left:30px; margin-right: 30px; margin-top: 10px; }
    &>.btnDv{ margin-left:30px; margin-right: 30px;}

    .tabContainer .page{margin: 20px 0 0;}

    &.wide{ 
     width: 96%; left: 2%;
      .closeThis{right: 2%;}
    }

  }
  
  @media screen and (min-width: 1500px){
    &.wide{
      width: 1440px; left:calc( 50vw - 720px );

      .closeThis{right: calc( 50vw - 720px);}
      
    }
  }
}




// 20201209 要在燈箱中開啟的頁面
.forLightbox{ margin: 0 3%;
  .head{margin: 0; overflow: hidden;
    border-bottom: 3px solid $border-color-pale;padding: 30px 0 .2em;
    h3{margin: 0;padding: 0; color: #000;}
    .openNewWin{float: right;  margin-top: -2.5em;}
  }
  .chartSet{
    .listTable , .listTbNew{padding-left: 0;padding-right: 0; margin-left: 0; margin-right: 0;}
    .formLayout td:nth-child(even) select{width: auto; min-width: 20em;}
  }
  .listTable{
    padding-left: 0;padding-right: 0; margin-left: 0; margin-right: 0;
    // width: 100%; margin-left: auto; margin-right: auto;
  }

  @media screen and (min-width: $wwMedium){
    margin-left: 30px;margin-right: 30px;
  }
}
