body{
  margin: 0 auto;padding:0;
  font-size: 18px;
  font-family:'Red Hat Display', '微軟正黑體', Helvetica, Arial, sans-serif;color:$font-color;

  &.noScroll{ overflow: hidden; overflow-y:hidden; }

  @media screen and (min-width:$wwMedium){
    font-size: 15px;
  }
}

@media print{
  body{background: none; font-size: 10pt; color: #000;}
}


*:focus{outline: none}
a{color: $font-a-color;text-decoration: none;border-bottom: 1px solid $border-color-pale;cursor: pointer;}
a:hover{color: $font-ahover-color;border-bottom-color:$font-ahover-color}
em{font-style: normal;}
table{margin: 0 auto;border-collapse: collapse;line-height: 1.5;}

button{cursor: pointer;}

h1, h2, h3, h4, h5, h6 {color: $head-font-color;padding: 0;margin: 1.5em 0 .5em;}
h1{font-size: 2em;font-weight: normal;} // 2025
h2{font-size: 1.86em;font-weight: normal;}
h3{font-size: 1.6em;font-weight: normal;}
h4{font-size: 1.2em;}
h5{font-size: 1.1em;}
h6{font-size: 1em;}
h1, h2, h3{line-height: 1.2;}
h4, h5, h6 {line-height: 1.3;}

// 20210421 覆蓋 _froala.min.css  的設定
body .fr-view .fr-file::after{display: none;} // 隱藏檔案連結後面的奇怪圖示